body {margin:0;}

.navbar {
  overflow: hidden;
  background-color: rgb(11, 83, 4);
  position: fixed;
  top: 0;
  width: 100%;
}

.navbar a {
  float: right;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}
button{
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  overflow: hidden;
  background-color: #333;
   top: 0;
 

}
.navbar a:hover {
  background: #ddd;
  color: rgb(27, 104, 117);
}

ul
{
  
  float:right;
  width:100%;
  padding:0;
  margin:0;
  list-style-type:none;
}
.div-img {
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  color: #f2f2f2;
}
*, *:after, *:before {
  box-sizing: border-box;
}
.color-container {
  display: flex;
}
.color {
  flex-basis: 50%;
  height: 100vh;
  cursor: pointer;
  position: relative;
}

.color__code {
  position: left;
  
  background-color: rgba(0,0,0,0.4);
  color: white;
  font-family: Helvetica, sans-serif;
  letter-spacing: 0.1rem;
  font-size: 1.25em;
  font-weight: 300;
  text-transform: uppercase;
  z-index: 1;
}
.color__code::-moz-selection {
  background-color: transparent;
}
.color__code::selection {
  background-color: transparent;
}

@media screen and (max-width: 550px) {
  .color-container {
    flex-wrap: wrap;
  }
  .color {
    height: 50vh;
  }
}



